<template>
  <transition name="confirm-fade">
    <div v-if="isShowConfirm" class="my-confirm" @click.stop="clickFun('clickCancel')">
      <div class="confirm-content-wrap" @click.stop>
        <h3 class="my-confirm-title">{{ titleText }}</h3>
        <p class="my-confirm-content">{{ content }}</p>
        <div class="my-operation">
          <div v-if="type==='confirm'" class="my-cancel-btn" @click="clickFun('clickCancel')">
            <p class="my-btn-text my-border-right">{{ cancelText }}</p>
          </div>
          <div class="confirm-btn" @click="clickFun('clickConfirm')">
            <p class="my-btn-text">{{ confirmText }}</p>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
	import axios from 'axios'
export default {
  data () {
    return {
      isShowConfirm: false, // /
      titleText: '', // 
      content: 'Say Something ...', // 
      cancelText: '', // 
      confirmText: '', // 
      type: 'confirm', // confirm - alert - 
      outerData: null, // userBehavior
      id:''
    }
  },
  methods: {
  	getData(){
  		var api=this.$root.aurl+'api/user/trade_list';
   		 axios.get(api).then((res)=>{
   		 	//console.log(res)
   		 	this.celue = res.data.data;
   		 })
  	},
    show (titleText,content,e, config) {
      this.content = content || 'Say Something ...';
      this.titleText = titleText || '';
	  this.id = e;
      if (Object.prototype.toString.call(config) === '[object Object]') {
        // 
        this.titleText = config.titleText || ''
        this.cancelText = config.cancelText || ''
        this.confirmText = config.confirmText || ''
        this.type = config.type || 'confirm'
        this.outerData = config.data || null
      }

      this.isShowConfirm = true
    },
    hidden () {
      this.isShowConfirm = false
      this.titleText = ''
      this.cancelText = ''
      this.confirmText = ''
      this.type = 'confirm'
      this.outerData = null
    },
    clickFun (type,e) {
     var that = this;
     if(type == 'clickConfirm'){
     	var api=this.$root.aurl+'api/user/tosell';
     	axios.get(api,{params:{'id':this.id,}}).then((res)=>{
        that.$toast.center(res.data.message);
        this.getData();
        })
     }
      this.hidden()
    }
  }
}
</script>

<style scoped>
  .my-confirm {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 998;
    /*  iPhone  */
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }

  /*  */
  .confirm-fade-enter-active {
    animation: opacity 0.3s;
  }
  .confirm-fade-enter-active .confirm-content-wrap {
    animation: scale 0.3s;
  }
  .confirm-fade-leave-active {
    animation: outOpacity 0.2s;
  }

  /*  */
  .confirm-content-wrap {
    position: absolute;
    top: 28%;
    left: 0;
    right: 0;
    width: 280px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 5px;
    z-index: 999;
    user-select: none;
  }

  /*  */
  .my-confirm-title {
    padding-top: 20px;
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color: #333;
  }

  /*  */
  .my-confirm-content {
    padding: 0 15px;
    padding-top: 20px;
    margin-bottom: 32px;
    text-align: center;
    font-size: 16px;
    color: #666;
    line-height: 1.5;
  }

  /*  */
  .my-operation {
    display: flex;
    border-top: 1px solid #eee;
  }
  .my-operation .my-cancel-btn, .confirm-btn {
    flex: 1;
  }
  .my-operation .confirm-btn {
    color: #ffb000;
  }
  .my-operation .my-btn-text {
    text-align: center;
    font-size: 16px;
    margin: 14px 0;
    padding: 6px 0;
  }

  /*  */
  .my-border-right {
    border-right: 1px solid #eee;
  }

  /*  */
  @keyframes opacity {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes scale {
    0% {
      transform: scale(0);
    }
    60% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }

  /*  */
  @keyframes outOpacity {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
</style>


